@import "./../../../scss/exports";

.drop-wrapp {
  select {
    @extend .label-title;
    box-sizing: border-box;
    width: 184px;
    height: 32px;

    font-size: 12px;
    border: 1px solid #c5d7ff;
    border-radius: 2px;
    padding: 0 20px 0 0px;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    font-size: 14px;
    color: #000000;
    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
    background: url(http://astra.site4u.top/assets/img/select-grey-icon.svg) no-repeat #fff;
    background-position-x: right;
    background-position-y: center;
    background-position: right 8px center;
  }
}