@import "./../../scss/varibals";

.ItemDetils-wrapp {
  padding: 0 16px 0 5px;

  a {
    color: $controls_blue;
    text-decoration: none;
  }

  .ItemDetils {
    color: $text_grey;
    min-height: 64px;
    background: $bg_litegrey;
    box-shadow: 0px 2px 10px rgba(22, 84, 152, 0.1);
    border-radius: 4px;
    margin-bottom: 8px;
    border-left: 4px solid transparent;

    .icon {
      img {
        max-width: 100%;
        height: auto;
      }
    }

    &.item-added {
      border-left: 4px solid $global_blue;
      color: $notactive_litegrey;
    }

    &.item-row-vehicles {

      .label {
        font-family: "Ubuntu", sans-serif;
        font-style: normal;
        font-weight: 300;
        font-size: 13px;
        line-height: 15px;
        padding: 8px 4px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;

        &.add-btn {
          width: 15%;
          padding-right: 24px;
          align-items: flex-end;
        }

        &.delete-btn {

          display: flex;
          align-items: center;
          justify-content: end;
          flex-direction: row;
        }
      }
    }

    &.item-row-equipments {
      display: flex;
      justify-content: space-around;
      flex-direction: row;
      flex-wrap: nowrap;

      .label {
        font-family: "Ubuntu", sans-serif;
        font-style: normal;
        font-weight: 300;
        font-size: 13px;
        line-height: 15px;
        padding: 8px 4px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;

        &.add-btn {
          padding-right: 24px;
          align-items: flex-end;
        }

        &.delete-btn {
          display: flex;
          align-items: center;
          justify-content: space-around;
          flex-direction: row;
        }
      }
    }
  }
}