.user-card-wrapp {
  margin-left: 20px;
  margin-top: 4px;
  display: flex;
  align-items: center;

  .user-name {
    select {
      border: none;
    }
  }

  .user-name {
    font-family: Ubuntu;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
  }

  .avatar {
    border-radius: 50%;
    width: 40px;
    height: 40px;
    overflow: hidden;
    margin-right: 8px;

    img {
      width: 100%;
    }
  }
}