@import "./../../scss/exports";

.modal-header-wrapp {
  box-sizing: border-box;
  height: 68px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  // padding: 16px;
  overflow: hidden;

  .left-part {
    height: 100%;
    width: 50%;
    padding-left: 16px;

    h5 {
      @extend .label-title
    }
  }

  .right-part {
    height: 100%;
    border-left: 0.5px solid #dbdce7;
    width: 50%;
    display: flex;


  }
}