@import "./../../scss/varibals";

.list-header {
  padding: 0 16px 0 5px;
  color: $notactive_litegrey;
}

.item-row-equipments {
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  flex-wrap: nowrap;

  .label {
    font-family: "Ubuntu", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 15px;

    padding: 16px 4px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;

    &:first-child {
      width: 15%;
    }

    &:nth-child(2) {
      width: 20%;
    }

    &:nth-child(3) {
      width: 20%;
    }

    &:nth-child(4) {
      width: 14%;
    }

    &:nth-child(5) {
      width: 12%;
    }

    &:nth-child(6) {
      width: 19%;
    }

  }
}

.item-row-vehicles {
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  flex-wrap: nowrap;

  .label {
    font-family: "Ubuntu", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 15px;
    padding: 16px 4px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;

    &:first-child {
      width: 15%;
    }

    &:nth-child(2) {
      width: 7%;
    }

    &:nth-child(3) {
      width: 10%;
    }

    &:nth-child(4) {
      width: 20%;
    }

    &:nth-child(5) {
      width: 15%;
    }

    &:nth-child(6) {
      width: 15%;
    }

    &:last-child {
      width: 15%;
    }
  }

  &.worker-list {
    .label {
      &:nth-child(4) {        
        width: 32%;
        flex-direction: row !important;
        align-items: center !important;
        justify-content: flex-start !important;
      }

      &:last-child {
        width: 5%;
      }
    }
  }
}