.app {
  font-family: "Ubuntu", sans-serif;
  color: #444444;
  font-size: 13px;
}

button,
input,
select,
a {
  &:hover {
    cursor: pointer;
  }

  &:focus {
    outline: none;
  }
}

button,
a {
  transition: opacity 0.2s;

  &:hover {
    opacity: 0.7;
  }
}

* {
  box-sizing: border-box;
}