@import "./../../../scss/varibals";

.quantity-input {
  font-family: "Ubuntu", sans-serif;
  width: 48px;
  position: relative;

  input {
    width: 100%;
    height: 32px;
    padding: 8px 24px 8px 8px;
    background: $bg_litegrey;
    border: 1px solid #e0e0e0;
    box-sizing: border-box;
    border-radius: 2px;
  }

  button {
    border: none;
    position: absolute;
    right: 0;
    width: 16px;
    height: 16px;
    background: $controls_blue;
    color: #fff;
    text-align: center;
    box-sizing: border-box;
    padding: 0 0px;
    cursor: pointer;
  }

  .quantity-input-decrement {
    bottom: 0;
    border-radius: 0px 0px 2px 0px;
    font-size: 10px;
  }

  .quantity-input-increment {
    top: 0;

    border-radius: 0px 2px 0px 0px;
  }
}