.Search-wrapp {
  input {
    height: 32px;
    width: calc(100% - 12px);
    border: 1px solid #c5d7ff;
    border-radius: 99px;
    padding: 8px 12px;
    background-image: url("http://astra.site4u.top/assets/img/icon-search.png");
