@import "./../../scss/exports";

.modal-footer-wrapp {
  padding: 12px 16px 25px 16px;

  .modal-footer {
    height: 68px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 40px;

    .drop-wrapp {
      select {
        @extend .label-title;
        box-sizing: border-box;
        width: 184px;
        height: 32px;
        // font-weight: 600;
        // font-size: 12px;
        border: 1px solid #c5d7ff;
        border-radius: 2px;
        padding: 0 20px 0 12px;
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        font-size: 14px;
        color: #000000;
        transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
        background: url("http://astra.site4u.top/assets/img/select-grey-icon.svg") no-repeat #fff;
        background-position-x: right;
        background-position-y: center;
        background-position: right 8px center;
      }
    }
  }

  .btn-group-wrapp {
    .cancel-btn {
      font-family: Ubuntu;
      font-style: normal;
      font-weight: bold;
      font-size: 16px;
      line-height: 18px;
      color: $controls_blue;
      border: none;
      background: none;
      padding: 0 70px;
      height: 40px;
    }

    .submit-btn {
      height: 40px;
      background: $controls_blue;
      border: 1px solid $controls_blue;
      box-sizing: border-box;
      box-shadow: 0px 4px 10px rgba(20, 88, 156, 0.1);
      border-radius: 2px;
      padding: 0 70px;
      font-family: Ubuntu;
      font-style: normal;
      font-weight: bold;
      font-size: 16px;
      line-height: 18px;
      color: $bg_litegrey;
      margin-left: 4px;
    }
  }
}

/* CAUTION: IE hackery ahead */
select::-ms-expand {
  display: none;
  /* remove default arrow on ie10 and ie11 */
}

/* target Internet Explorer 9 to undo the custom arrow */
@media screen and (min-width: 0\0) {
  select {
    background: none\9;
    padding: 5px\9;
  }
}