@import "./../../scss/varibals";

.ModalContainerWrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  background: rgba(0, 0, 0, 0.5);
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  .modal-dialog-wrapp {
    background: $bg_litegrey;
    box-shadow: 0px 2px 10px rgba(22, 84, 152, 0.1);
    border-radius: 4px;
    width: 1090px;

    .lists-wrapp {
      display: flex;
      background: #f2f3f8;

      .left-list,
      .right-list {
        padding: 24px 4px 12px 16px;
        width: 50%;
      }

      .right-list {
        border-left: 0.5px solid #dbdce7;
      }
    }
  }
}